body, html {
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f5f5f5;
    font-family: Arial, sans-serif;
}

.centered-div {
    background-color: #ffffff;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    text-align: center;
    min-width: 500px;
    max-width: 700px;
    margin: 10px;
    word-break: break-word;
}

.divWithBorder {
    background-color: #ffffff;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    text-align: center;
    min-width: 500px;
    max-width: 700px;
    margin: 10px;
    word-break: break-word;
    border: 2px solid darkslategray; /* Рамка 2px синего цвета */
}

img {
    max-width: 100%;
    height: auto;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 10px 0;
    color: #333;
}

h1 {
    font-size: 2.5em;
    font-weight: 700;
    color: #2c3e50;
    border-bottom: 2px solid #2c3e50;
    padding-bottom: 10px;
    margin-bottom: 20px;
}

h2 {
    font-size: 2em;
    font-weight: 600;
    color: #34495e;
    padding-bottom: 8px;
    margin-bottom: 18px;
}

h3 {
    font-size: 1.75em;
    font-weight: 500;
    color: #5d6767;
    padding-bottom: 6px;
    margin-bottom: 16px;
}

h4 {
    font-size: 1.5em;
    font-weight: 500;
    color: #95a5a6;
    margin-bottom: 14px;
}

h5 {
    font-size: 1.25em;
    font-weight: 400;
    color: #bdc3c7;
    margin-bottom: 12px;
}

h6 {
    font-size: 1em;
    font-weight: 400;
    color: #ecf0f1;
    margin-bottom: 10px;
}

.styled-table {
    width: 100%;
    border-collapse: collapse;
    font-size: 16px;
    font-family: Arial, sans-serif;
    text-align: left;
    margin: 20px 0;
}

.styled-table thead tr {
    background-color: #009879;
    color: #ffffff;
    text-align: left;
}

.styled-table th {
    padding: 12px 15px;
    border: 1px solid #dddddd;
    font-style: italic;
    text-align: center;
}

.styled-table td {
    padding: 12px 15px;
    border: 1px solid #dddddd;
}

.styled-table tbody tr:nth-child(even) {
    background-color: #f3f3f3;
}

.styled-table tbody tr:hover {
    background-color: #f1f1f1;
}

.styled-table tbody tr:last-of-type {
    border-bottom: 2px solid #009879;
}
